*, *:before, *:after {
  box-sizing: inherit; }

body {
  font-family: "akkurat", sans-serif;
  font-size: 16px !important;
  background: #fff; }

h1, h2, h3, h4, h5, h6 {
  font-family: "akkuratBold", sans-serif;
  margin: 0; }

a {
  transition: all 0.1s ease-in-out; }

.tcenter {
  text-align: center !important; }

.tleft {
  text-align: left !important; }

.tright {
  text-align: right !important; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.btn {
  padding: 0.5em 1em;
  border-radius: 25px;
  border: none;
  font-weight: 500;
  transition: all 0.1s ease-in-out; }
  .btn i {
    margin-left: 10px; }
  .btn.btn-primary {
    background: #fff;
    color: #000; }
  .btn.btn-secondary {
    background: #000;
    color: #fff; }
  .btn.btn-min {
    padding: 0.5em 1em; }
  .btn.btn-lg {
    font-size: 1.375em;
    padding: 0.5em 2em; }
  .btn.btn-thin {
    padding: 0.5em 3.5em; }

.cf:before,
.cf:after {
  content: " ";
  /* 1 */
  display: table;
  /* 2 */ }

.cf:after {
  clear: both; }

.cf {
  *zoom: 1; }

.c-black {
  color: #000 !important; }

.container {
  display: block;
  margin: 0 auto;
  height: auto; }

section.main {
  padding: 2em 0; }

.section-wrap {
  padding: 6em 0;
  position: relative; }
  .section-wrap.nopad {
    padding: 0; }
  .section-wrap .image-content {
    display: block;
    margin-bottom: 1em; }
  .section-wrap > .container {
    height: 100vh; }

.title-page {
  padding: 0 0 0.5em;
  margin: 0 0 2em;
  vertical-align: middle; }
  .title-page.absolute {
    position: absolute;
    top: -60px;
    z-index: 5;
    display: none; }
  .title-page h2 {
    display: inline-block;
    color: white;
    font-size: 1.5em;
    font-weight: 600; }
    .title-page h2.subtitle {
      font-weight: 400; }

.cnt-table {
  display: table;
  position: relative;
  width: 100%;
  height: 100%; }

.cnt-table-cell {
  display: table-cell;
  vertical-align: middle;
  height: 100%;
  width: 100%;
  /*text-align: center;*/ }
  .cnt-table-cell.top {
    vertical-align: top; }

#one-img {
  background: url("../img/img_home_princ.jpg");
  background-size: cover;
  background-attachment: fixed; }
  #one-img h1 {
    color: #fff;
    margin: 3em 0 0 0;
    text-align: center; }

#quieneses_somos.section-wrap {
  background: #1e1e1e;
  color: #fff;
  font-size: 1.500em; }

#confian.section-wrap {
  background: #fff; }
  #confian.section-wrap .title-page {
    text-align: center;
    margin-bottom: 2.5em; }
    #confian.section-wrap .title-page h2.subtitle {
      color: #000; }
  #confian.section-wrap .list-logos {
    text-align: center;
    margin: 3em 0; }
    #confian.section-wrap .list-logos li {
      display: inline-block;
      vertical-align: middle;
      margin: 0 1%;
      width: 30%;
      text-align: center; }
      #confian.section-wrap .list-logos li img {
        margin: 0 auto; }

#como_hacemos.section-wrap {
  background: url("../img/bg_contacto.jpg") no-repeat center center;
  background-size: cover;
  background-attachment: fixed;
  color: #fff; }

.item-hacemos {
  background: #fff;
  position: relative;
  box-shadow: 0 0px 1px rgba(0, 0, 0, 0.3);
  border-radius: 3px;
  overflow: hidden;
  transition: all 0.1s ease-in-out;
  display: block;
  padding: 1em;
  min-height: 370px; }
  .item-hacemos:hover {
    box-shadow: 0 14px 56px -20px rgba(0, 0, 0, 0.64);
    transition-timing-function: ease-out;
    transition-duration: .15s;
    transform: translateY(-3px); }
  .item-hacemos h3 {
    font-size: 1.5em;
    text-align: center;
    margin-bottom: 1em; }
  .item-hacemos .image {
    text-align: center;
    margin: 1em 0 2em; }
    .item-hacemos .image img {
      margin: 0 auto; }
  .item-hacemos .detail ul {
    list-style: disc;
    padding: 0 0 0 20px; }

.item-comohacemos {
  margin-bottom: 3.5em; }
  .item-comohacemos .image {
    float: left; }
  .item-comohacemos .detail {
    padding: 0 0 0 90px;
    color: #000; }
    .item-comohacemos .detail h3 {
      text-transform: uppercase;
      margin-bottom: 0.5em; }

.cnt-btn .btn {
  font-size: 1.375em; }
  .cnt-btn .btn:hover {
    box-shadow: 0 14px 56px -20px rgba(0, 0, 0, 0.64);
    transition-timing-function: ease-out;
    transition-duration: .15s; }
  .cnt-btn .btn:active:hover {
    background: #fff;
    color: #000; }

.form-control {
  border: 1px solid #ccc;
  padding: 0.6em;
  border-radius: 3px;
  box-shadow: none;
  height: 40px; }

.modal-header {
  border: none; }
  .modal-header .close {
    position: absolute;
    right: 10px;
    top: 10px; }
  .modal-header .image {
    text-align: center; }
    .modal-header .image img {
      margin: 1em auto; }
  .modal-header h5 {
    text-align: center;
    color: #9b9b9b;
    font-size: 1em;
    font-weight: 400;
    font-family: "ProximaNova", sans-serif; }

#mdlContacto .modal-body {
  padding: 15px 4em; }
  #mdlContacto .modal-body .form-btn {
    margin-top: 1.5em; }

#mdlContacto textarea {
  min-height: 120px; }

#header {
  background: white; }
  #header .navbar-fixed-top {
    background: #fff;
    box-shadow: 0 14px 56px -20px rgba(0, 0, 0, 0.1); }
  #header .container {
    padding-top: 1.5em;
    padding-bottom: 1.5em; }
  #header .logo {
    margin: 0; }
    #header .logo a {
      width: 164px;
      height: 22px;
      display: block;
      background: url("../img/logo_color.png") no-repeat;
      background-size: contain;
      text-indent: -9999em; }
  #header .menu {
    margin: 0 0;
    float: right; }
    #header .menu li a {
      padding: 0 0 0 25px;
      color: #7f7c80;
      font-size: 1.125em;
      text-decoration: none; }
    #header .menu li:hover a {
      color: #000; }
    #header .menu li.active a {
      font-family: "akkuratBold";
      color: #000; }

/* Navigation Toggle
-------------------------------------------------------*/
.navbar-toggle {
  margin-top: 0;
  margin-bottom: 0;
  padding: 5px 10px;
  border: none;
  z-index: 50; }
  .navbar-toggle .icon-bar {
    background-color: #999;
    width: 18px; }
  .navbar-toggle:focus .icon-bar,
  .navbar-toggle:hover .icon-bar {
    background-color: #000; }
  .navbar-toggle.collapsed .icon-bar {
    background-color: #999; }

#footer {
  background: #000;
  padding: 3em 0 2em; }
  #footer .block-list {
    margin: 0 0 2em; }
    #footer .block-list.menu ul li a {
      text-transform: uppercase; }
    #footer .block-list ul li {
      margin-bottom: 1em;
      color: #fff;
      font-size: 1.250em;
      font-family: akkuratLight; }
      #footer .block-list ul li a, #footer .block-list ul li span {
        color: #fff; }
    #footer .block-list ul.social li {
      display: inline-block; }
      #footer .block-list ul.social li a {
        height: 24px;
        width: 24px;
        display: block;
        border-radius: 100%;
        background: #fff;
        color: #000;
        text-align: center;
        line-height: 24px; }
    #footer .block-list p {
      color: #fff;
      font-size: 12px; }

#footer-bottom {
  background: #000;
  padding: 2em 0; }
  #footer-bottom .copyright {
    color: #fff;
    font-size: 1.250em;
    font-family: akkuratLight;
    margin: 0; }
  #footer-bottom .logo {
    float: right; }

@media (min-width: 768px) {
  #mdlContacto .modal-dialog {
    width: 552px; } }

@media (max-width: 991px) {
  body {
    font-size: 14px !important; }
  .navbar-collapse.collapse {
    display: none !important; }
  .navbar-nav {
    margin-top: 7.5px; }
  .navbar-nav > li {
    float: none; }
  .navbar-nav > li > a {
    padding-top: 10px;
    padding-bottom: 10px; }
  .collapse.in {
    display: block !important; }
  .navbar-header {
    float: none; }
  .navbar-toggle {
    display: block; }
  .nav-wrap {
    width: 100%;
    padding: 0;
    min-height: 0; }
  .navbar-header {
    width: 100%;
    padding-right: 0; }
  .navbar-nav {
    width: 100%;
    padding: 10px 0; }
  .navigation-overlay.sticky .navbar-nav > li > a,
  .navigation-overlay .navbar-nav > li > a {
    padding: 10px 0;
    line-height: 30px; }
  .section-wrap {
    padding: 6em 0;
    position: relative; }
    .section-wrap.nopad {
      padding: 4em 0; }
  #one-img h1 br {
    display: none; }
  .section-wrap > .container {
    height: auto; }
  .item-hacemos {
    margin-bottom: 2em; }
  #confian.section-wrap .list-logos li {
    margin: 3em 0;
    width: 100%;
    display: block; }
  #footer .block-list ul li {
    margin: 0.5em 0; }
  #footer .block-list.menu ul {
    text-align: center; }
    #footer .block-list.menu ul li {
      display: inline-block;
      margin: 0.5em 1.5em; }
  #footer-bottom .logo {
    float: none;
    text-align: center;
    margin: 1em 0 0 0; }
    #footer-bottom .logo img {
      margin: 0 auto; }
  #footer-bottom .copyright {
    text-align: center; } }
